import React from "react";
import PropTypes from "prop-types";

// components

// generates the amount of stars to show for the rating for each shop
function renderStars(len) {
  if (len < 1) {
    return null;
  }
  const stars = [];
  for (let i = 0; i < Math.floor(len); i++) {
    stars.push(
      <i key={i} className="fas fa-star" style={{ color: "#a37eba" }}></i>
    );
  }
  return stars;
}

// For the logged in user, send the clicked item and its data to the server
// to essentially post that shop to the DB
async function onClick(e, shopName, image_url, url, phone, address, rating, id, userInformation) {
  /**
   * Prod: https://coffee-connection.herokuapp.com/bookmarks/bookmark
   * Dev: `http://localhost:5000/bookmarks/bookmark`
   */
  const postShop = await fetch(`https://coffee-connection.herokuapp.com/bookmarks/bookmark`, {
    method: 'POST',
    headers : {
      'Content-Type': 'application/json'
    },
    // body of the request being sent (contains all coffee shop info +USER ID IMPORTANT!!)
    body: JSON.stringify({
      shopName,
      image_url,
      url,
      phone,
      address,
      rating,
      shopID: id,
      userID: userInformation.id
    })
  });
  const res = await postShop.json();
  console.log(res)
}

// takes in props from parent component
function LocationItem({
  shopName,
  image_url,
  url,
  phone,
  address,
  rating,
  id,
  isAuthenticated,
  userInformation
}) {
  return (
    <tr>
      <td>{shopName}</td>
      <td>
        <div>
          {/* Only if the user is authenticated, show the bookmark */}
          {isAuthenticated ? (
            <div
            className="tooltip__icon__container"
            data-tooltip="Bookmark this coffee shop to your profile"
            data-position="left center"
          >
            <i
              data-shop__id={id}
              onClick={e => onClick(e, shopName, image_url, url, phone, address, rating, id, userInformation)}
              className="fas fa-bookmark"
              id="bookmarker"
            ></i>
          </div>
          ): null}
          <ul className="shop__list__container">
            <li>
              <img src={image_url}></img>
            </li>
            <div className="sub__division">
              <li>{phone}</li>
              <li>{address}</li>
              <li>
                {renderStars(rating).map((star) => {
                  return star;
                })}
              </li>
              <li>
                <a href={url} target="_blank">
                  See on Yelp!
                </a>
              </li>
            </div>
          </ul>
        </div>
      </td>
    </tr>
  );
}

// Prop validation
LocationItem.propTypes = {
  shopName: PropTypes.string.isRequired,
  image_url: PropTypes.string.isRequired,
  url: PropTypes.string.isRequired,
  phone: PropTypes.string.isRequired,
  address: PropTypes.string.isRequired,
  rating: PropTypes.number.isRequired,
  id: PropTypes.string.isRequired,
};

export default LocationItem;
