import React from 'react';

function renderStars(len) {
  if (len < 1) {
    return null;
  }
  const stars = [];
  for (let i = 0; i < Math.floor(len); i++) {
    stars.push(
      <i key={i} className="fas fa-star" style={{ color: "#a37eba" }}></i>
    );
  }
  return stars;
}

function BookmarkItem({shop, removeBookmark}) {
  console.log(shop);
  return (
      <div className="ui card" style={{width: "400px", height: "420px", marginTop: "none"}}>
        <div style={{width: "100%", height: "280px", textAlign: "center", marginBottom: "1rem"}}>
          <img src={shop.imageURL} style={{width: "100%", height: "100%", objectFit: "cover", marginTop: "1rem"}}/>
        </div>
        <div className="content" style={{marginTop: "1rem"}}>
          <div className="header">{shop.name}</div>
          <div>{shop.address}</div>
        </div>
        <div className="extra content">
          <span className="right floated">
          {renderStars(Math.ceil(shop.rating)).map((star) => {
            return star;
          })}
          </span>
          <span>
            <i className="fas fa-trash-alt trashbin" 
            style={{width: '30px', height: '30px'}}
            onClick={removeBookmark.bind(this, shop.shopID)}
            ></i>
          </span>
        </div>
      </div>
  )
}

export default BookmarkItem;
